import moment from 'moment';

// Note: ,       , , 
export function dataTime({ valueStart = new Date(), valueEnd = new Date(), startOf = 'day', endOf = 'day', format = 'YYYY-MM-DD HH:mm:ss' } = {}) {
    return {
        start_time: moment(valueStart).startOf(startOf).format(format),
        end_time: moment(valueEnd).endOf(endOf).format(format)
    }
}


